body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*:focus {
  outline: none;
}

body {
  background: -webkit-linear-gradient(left, #333, rgb(230, 151, 54), rgb(228, 64, 14));
  background: linear-gradient(to right, #333, rgb(230, 151, 54), rgb(228, 64, 14));
  line-height: 1.5;
}

.wrapper {
  display: grid;
  min-height: 80vh;
  max-width: 960px;
  height: 100%;
  grid-template-columns: 2fr;
  margin: 50px auto;
  box-shadow: 5px 10px 30px #000;
}

.left {
  background: url('https://cdn.dribbble.com/users/968424/screenshots/2287311/2015_10_12_flatlandscape_800x600_v01.jpg');
  background-size: cover;
  text-align: center;
}

.right {
  background: #333;
  text-align: center;
}

@media ( min-width: 470px) {
  .wrapper {
    height: 100%;
    width: 100%;
    grid-template-columns: .5fr 1fr; 
  }
}

@media ( min-width: 560px) {
  .wrapper {
    height: 100%;
    width: 100%;
    grid-template-columns: 2fr 1fr; 
  }
}
form {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 30vh;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.form-input {
    width: 100%;
}

form input {
    font-size: 1.25em;
    padding: .5em 1em;
    outline: none;
    border: none;
    width: 80%;
    margin: .5em 0;
}

form button {
    width: 80%;
    padding: .5em 1em;
    font-size: 1em;
}
h1 {
    color: #333;
    font-size: 3em;
}
p {
    font-size: 1.5em;
    color: #ccc;
}

p span {
    color: orangered;
}

.weatherIcon {
    width: 150px;
    height: auto;
}
